.DrawerToggle__DrawerToggle__24-HS {
    margin-left: 2.5%;
    width: 60px;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    padding: 11px 0;
    box-sizing: border-box;
    cursor: pointer;
    background-color: white;
}

.DrawerToggle__DrawerToggle__24-HS div{
    width: 55%;
    height: 2.5px;
    background-color: #222222;
    box-sizing: border-box;
}

@media (min-width: 530px) {
    .DrawerToggle__DrawerToggle__24-HS {
        display: none;
    }
}
