.Education__Content__AlHKS {
    width: 100%;
    margin: 0px;
    padding: 0;
    display: block;
    box-sizing: border-box;
    -webkit-animation-name: Education__contentMove__Mj4Ep;
            animation-name: Education__contentMove__Mj4Ep;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;

}

.Education__Content__AlHKS h2 {
    text-align: center;
}
.Education__Content__AlHKS ul {
    width: 100%;
    margin-top: 45px;
    padding: 0px;
    list-style: none;
}

@media screen and (max-width: 800px) {
  .Education__Content__AlHKS {
      width: 100%;
      margin: 25% 0;
      height: auto;
      padding: 0px 15px 30px 0;
  }
}

@-webkit-keyframes Education__contentMove__Mj4Ep {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes Education__contentMove__Mj4Ep {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}
