.Homepage__Content__3-T1a {
    width: 520px;
    margin: 5% 0px;
    display: block;
    box-sizing: border-box;
    -webkit-animation-name: Homepage__contentMove__3-Ygt;
            animation-name: Homepage__contentMove__3-Ygt;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}

h1 {
  color: salmon;
  text-transform: uppercase;
}

@media screen and (max-width: 800px) {
  .Homepage__Content__3-T1a {
      width: 100%;
      margin: 25% 0px;
  }
}

@-webkit-keyframes Homepage__contentMove__3-Ygt {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-1em);
                transform: translateY(-1em);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

@keyframes Homepage__contentMove__3-Ygt {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-1em);
                transform: translateY(-1em);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}
